import { Flip } from "./flip";
import React, { useState, useEffect } from 'react';
import s from './Counter.module.css'

export const Counter = (props) => {
    const [num, setNum] = useState(props.difference);
   
    
    //—Ñ—É–Ω–∫—Ü–∏—è —Å–∫–ª–æ–Ω—è–µ—Ç —Ü–∏—Å–ª–∏—Ç–µ–ª—å–Ω—ã–µ
    function num_word(value){ 
      let words = ['—à–∞–π–±–∞', '—à–∞–π–±—ã', '—à–∞–π–±'];
      value = Math.abs(value) % 100; 
      let num = value % 10;
      if(value > 10 && value < 20) return words[2]; 
      if(num > 1 && num < 5) return words[1];
      if(num == 1) return words[0]; 
      return words[2];
    }
    
    useEffect(() => {
      setNum(props.difference)},
      [props.difference]
    );


    
    
 return (
  <div className={s.counter}>
  <h1>
    {props.isRuss ?  "–û–≤–µ—á–∫–∏–Ω—É –æ—Å—Ç–∞–ª–æ—Å—å –∑–∞–±–∏—Ç—å" : "Ovechkin needs" }
  </h1>
    <div className={s.flip}><Flip  value={num} /></div>
  <h1>
   {props.isRuss ? num_word(num) + " –¥–æ –∑–≤–∞–Ω–∏—è –ª—É—á—à–µ–≥–æ —Å–Ω–∞–π–ø–µ—Ä–∞ –≤ –∏—Å—Ç–æ—Ä–∏–∏ –ù–•–õ" 
   :  "goals to climb NHL all time high scoring record"}
   
  </h1>
  </div>
 )
}

